import React, { useEffect, useState } from "react";
import axios from "axios";

import Skeleton from "@material-ui/lab/Skeleton";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";

import RaspAutobusComponent from "./components/RaspAutobus";

import "./styles/IndexPage.sass";

export default function IndexPage() {
  const [buses, setBuses] = useState();

  useEffect(() => {
    fetchData();
  }, []);

  async function fetchData() {
    const data = await axios
      .get(`${process.env.REACT_APP_URL}/buses`)
      .then((res) => {
        const buses = res.data;
        const busesObj = {};
        for (let i = 0; i < buses.length; i++) {
          busesObj[buses[i].number] = buses[i];
        }
        return busesObj;
      });
    setBuses(data);
  }

  return (
    <div id="index-page">
      {buses ? <RaspAutobusComponent buses={buses} /> : <SkeletonBuseRasp />}
    </div>
  );
}

function SkeletonBuseRasp() {
  return (
    <div className="rasp-autobus">
      <div className="section-title">Bus schedule</div>

      <div className="profiles" style={{ marginBottom: "15px" }}>
        <div className="items">
          {[6, 14, 29].map((item, i) => (
            <Skeleton variant="rect" key={i} style={{ marginRight: "8px" }}>
              <div className="item">{item.name}</div>
            </Skeleton>
          ))}
        </div>
        <Skeleton variant="rect">
          <div className="edit item">
            <CompareArrowsIcon />
          </div>
        </Skeleton>
      </div>

      <div className="autobuses">
        <Skeleton variant="rect" style={{ width: "100%", margin: "0 auto" }}>
          <div className="route">Маршрут: 6</div>
        </Skeleton>
        {[1, 2].map((item, i) => (
          <div className="direction" key={i}>
            <Skeleton variant="rect">
              <div className="title">
                <CompareArrowsIcon /> Университет - Сквер Героя Карвата
              </div>
            </Skeleton>
            {[1].map((item, k) => (
              <div className="helt" key={k}>
                <Skeleton variant="rect">
                  <div className="title">Остановка: Университет</div>
                </Skeleton>
                <div
                  className="times"
                  style={{ justifyContent: "space-between" }}
                >
                  {[1, 2, 3].map((item, y) => (
                    <Skeleton variant="rect" key={y}>
                      <div className="item">
                        <div className="time">15:23</div>
                        <div className="time-to">через 15 минут</div>
                      </div>
                    </Skeleton>
                  ))}
                </div>
                <Skeleton variant="rect">
                  <div className="full-rasp">Показать полностью...</div>
                </Skeleton>
              </div>
            ))}
          </div>
        ))}
        <Skeleton variant="rect" style={{ maxWidth: "100%" }}>
          <div className="actions">
            <div className="add-direction">Добавить остановку</div>
          </div>
        </Skeleton>
      </div>
    </div>
  );
}
